<template>
  <div id="app">
    <div id="map">
      <div class="custom-control">
        <select name="drone" id="drone" v-model="drone">
          <option
            v-for="(drone, index) in droneModels"
            :key="index"
            :value="drone"
            >{{ drone.name }}</option
          >
        </select>
        <div class="input-wrapper">
          <label for="height">Высота:</label>
          <input type="number" v-model="height" name="height" :step="1" />
        </div>
        <div class="input-wrapper">
          <label for="angle">Поворот:</label>
          <input
            type="number"
            v-model="angle"
            name="angle"
            :step="1"
            v-on:change="changeAngle"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import L from 'leaflet'
import {
  coordAll,
  flip,
  bbox,
  square,
  bboxPolygon,
  distance,
  centroid,
  polygon,
} from '@turf/turf'
import 'normalize.css'
import 'leaflet/dist/leaflet.css'
import 'leaflet-draw'
import 'leaflet-draw/dist/leaflet.draw.css'
import { isNull } from 'util'

export default {
  name: 'app',
  data() {
    return {
      center: L.latLng(55.14454538318123, 37.456169128417976),
      url: 'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
      map: null,
      params: null,
      drone: null,
      features: null,
      polygon: null,
      height: 80,
      drawControl: null,
      bSquare: null,
      angle: 0,
      prevYaw: 0,
      polygonInit: {
        type: 'Feature',
        properties: {},
        geometry: {
          type: 'Polygon',
          coordinates: [
            [
              [37.44492530822754, 55.146507452160414],
              [37.44458198547363, 55.1383151747132],
              [37.46385097503662, 55.13833970500007],
              [37.46380805969238, 55.14626219881414],
              [37.44492530822754, 55.146507452160414],
            ],
          ],
        },
      },
      droneModels: [
        {
          name: 'Phantom 3 St,Adv,Pro, Phantom 4',
          widthPx: 4000,
          heightPx: 3000,
          width: 6.24,
          height: 4.68,
          focal: 3.61,
          pixelSize: 0.00156,
        },
        {
          name: 'Phantom 4Pro/Adv',
          widthPx: 5472,
          heightPx: 3648,
          width: 12.86,
          height: 8.57,
          focal: 8.57,
          pixelSize: 0.00235,
        },
        {
          name: 'Mavic 2 Pro',
          widthPx: 5472,
          heightPx: 3648,
          width: 13.19,
          height: 8.79,
          focal: 10.26,
          pixelSize: 0.00241,
        },
      ],
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.map = L.map('map').setView(this.center, 12)
      L.tileLayer(this.url, {
        attribution:
          '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      }).addTo(this.map)

      this.features = new L.FeatureGroup()
      this.map.addLayer(this.features)

      this.polygon = L.polygon(coordAll(flip(this.polygonInit))).addTo(
        this.features
      )

      this.bSquare = L.geoJSON(
        bboxPolygon(this.squreBbox(bbox(this.polygon.toGeoJSON())))
      ).addTo(this.features)

      this.drawControl = new L.Control.Draw({
        position: 'topright',
        draw: false,
        edit: {
          featureGroup: this.features,
        },
      })
      this.map.addControl(this.drawControl)

      this.initEvents()
    })
  },
  methods: {
    onEditVertex: function(e) {
      let box = bbox(e.poly.toGeoJSON())
      let fixedBox = this.squreBbox(box)
      let bPoly = bboxPolygon(fixedBox)
      this.polygon = e.poly
      this.bSquare.clearLayers()
      this.bSquare.addData(bPoly)
    },
    initEvents: function() {
      this.map.on('draw:editvertex', e => this.onEditVertex(e))
    },
    squreBbox: function(box) {
      let west = box[0]
      let south = box[1]
      let east = box[2]
      let north = box[3]

      let horizontal = distance(box.slice(0, 2), [east, south])
      let vertical = distance(box.slice(0, 2), [west, north])

      if (horizontal >= vertical) {
        let vertMidpoint = (south + north) / 2
        let horMidpoint = (west + east) / 2
        return [
          horMidpoint - (east - west),
          vertMidpoint - (east - west) / 2,
          horMidpoint + (east - west),
          vertMidpoint + (east - west) / 2,
        ]
      } else {
        let horMidpoint = (west + east) / 2
        let vertMidpoint = (south + north) / 2
        return [
          horMidpoint + (north - south),
          vertMidpoint + (north - south) / 2,
          horMidpoint - (north - south),
          vertMidpoint - (north - south) / 2,
        ]
      }
    },
    rotatePoints: function(center, points, yaw, prevYaw) {
      var res = []
      var angle = (yaw - prevYaw) * (Math.PI / 180)
      for (var i = 0; i < points.length; i++) {
        var p = points[i]
        // translate to center
        // rotate using matrix rotation
        var p3 = [
          center[0] +
            Math.cos(angle) * (p[0] - center[0]) -
            Math.sin(angle) * (p[1] - center[1]),
          center[1] +
            Math.sin(angle) * (p[0] - center[0]) +
            Math.cos(angle) * (p[1] - center[1]),
        ]
        // translate back to center
        // done with that point
        res.push(p3)
      }
      return res
    },
    projectPoints: function(points) {
      let res = []

      for (var i = 0; i < points.length; i++) {
        let p = this.map.project(L.latLng(points[i]), this.map.getZoom())
        res.push([p.x, p.y])
      }

      return res
    },
    unprojectPoints: function(points) {
      let res = []

      for (var i = 0; i < points.length; i++) {
        let p = this.map.unproject(L.point(points[i]), this.map.getZoom())
        res.push([p.lat, p.lng])
      }

      return res
    },
    changeAngle: function() {
      let bSquareGeoJSON = this.bSquare.toGeoJSON()
      let center = this.projectPoints([
        centroid(bSquareGeoJSON).geometry.coordinates,
      ])
      let projectedPoints = this.projectPoints(coordAll(bSquareGeoJSON))
      let rotatedPoints = this.rotatePoints(
        center[0],
        projectedPoints,
        this.angle,
        this.prevYaw
      )
      let unprojectedPoints = this.unprojectPoints(rotatedPoints)
      this.prevYaw = this.angle
      let newSquare = polygon([unprojectedPoints])
      this.bSquare.clearLayers()
      this.bSquare.addData(newSquare)
    },
  },
  components: {},
}
</script>

